* {
  box-sizing: border-box;
  color: #555;
  margin: 0;
  padding: 0;
}

html {
  background-color: #eee;
}

header {
  display: flex;

  vertical-align: middle;
  align-items: center;
  justify-content: space-between;

  background-color: #cdcdcd;
}

.title {
  width: 100%;
  text-align: center;
  margin: 4px;
}

.button {
  margin: 8px;
  padding: 8px;
  cursor: pointer;
}

.button:hover {
  background-color: #aaa;
  border-radius: 8px;
}

#main {
  padding: 8px;
}

/*taskList*/
.task-element-edge{
  margin: 8px 0;
}

.task-element {
  display: flex;
  justify-content: space-between;

  padding: 8px 16px 8px 16px;
  align-items: center;

  border-radius: 8px;

  background-color: #cdcdcd;
  cursor: pointer;
}

.task-element:hover {
  text-decoration: underline;
}

.task-title-desc {
  width: calc(100% - 64px);
}

.task-title-desc * {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* Modal */
.ReactModal__Content {
  height: fit-content;
}

.modal-frame div {
  margin-top: 8px;
}

.modal-title {
  border-bottom: 2px solid #555;
}

.modal-input {
  width: 100%;
}

.modal-submit {
  display: flex;
  justify-content: flex-end;
}

.modal-button {
  margin-left: 8px;
  padding: 4px;
}





